<template>
    <div id="paycenter-paybill">
        <public-header title="账单"></public-header>
        <div class="content">
            <van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
                <van-list
                        v-model="records.loading"
                        :finished="records.finished"
                        @load="onLoad"
                        :offset="100"
                        :immediate-check="false"
                        v-if="!records.empty"
                        class="bill-list"
                >
                    <div class="bill-item" v-for="(item, index) in records.data">
                        <div class="padding-10 flex-lr">
                            <div class="bill-title flex">
                                <div class="bill-img">
                                    <img :src="item.avatar" alt="">
                                </div>
                                <div class="bill-name">{{item.nickname}}</div>
                            </div>
                            <div class="itag" :class="{'itag-info': item.pay_type == 'alipay', 'itag-primary': item.pay_type == 'wechat', 'itag-danger': item.pay_type == 'credit'}">{{item.pay_type_cn.text}}</div>
                        </div>
                        <div class="border-1px-t padding-10">
                            <div class="bill-info flex">
                                <div>顾客支付：</div>
                                <div class="c-danger">￥{{item.final_fee}}</div>
                            </div>
                            <div class="bill-info flex">
                                <div>最终收入：</div>
                                <div class="c-danger">￥{{item.store_final_fee}}</div>
                            </div>
                            <div class="bill-info flex">
                                <div>下单时间：</div>
                                <div>{{item.addtime_cn}}</div>
                            </div>
                            <div class="bill-info flex">
                                <div>订单编号：</div>
                                <div>{{item.order_sn}}</div>
                            </div>
                            <div class="bill-info flex">
                                <div>下单店铺：</div>
                                <div class="c-primary">{{item.store_title}}</div>
                            </div>
                        </div>
                    </div>
                    <div class="loaded" v-if="records.finished">
                        <div class="loaded-tips">没有更多了</div>
                    </div>
                </van-list>
                <div class="no-data" v-else>
                    <img src="static/img/order_no_con.png" alt="">
                    <p>没有符合条件的数据!</p>
                </div>
            </van-pull-refresh>
        </div>
        <iloading v-if="showPreLoading"></iloading>
    </div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				isRefresh: false,
				showPreLoading: true,
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad(force){
				getRecords({
					vue: this,
					force: force,
					url: 'plateform/paycenter/paybill/list'
				});
			},
			onPullDownRefresh(){
				this.onLoad(true);
			},
		},
		mounted(){
			this.onLoad();
		},
	}
</script>

<style>
    #paycenter-paybill .content .bill-item{
        background-color: #fff;
        margin-bottom: 10px;
    }
    #paycenter-paybill .content .bill-title .bill-img{
        width: 35px;
        height: 35px;
    }
    #paycenter-paybill .content .bill-title .bill-img img{
        width: 100%;
        height: 100%;
        border-radius: 100%;
    }
    #paycenter-paybill .content .bill-title .bill-name{
        font-weight: bold;
        font-size: 16px;
        padding-left: 10px;
    }
    #paycenter-paybill .content .bill-item .bill-info{
        font-size: 14px;
        color: #7a7a7a;
        padding-bottom: 8px;
    }
    #paycenter-paybill .content .bill-item .bill-info:last-child{
        padding-bottom: 0;
    }
</style>


